// В этом файле должны быть стили только для БЭМ-блока alert, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, $media-условий...
// Не пишите здесь другие селекторы.

$gray-lighter:                hsl(0, 0%, 80%) !default;
$border-color:                #bababa;

$text-color--muted:           hsl(0, 0%, 50%) !default;

$line-height:                 1.375em !default;

$field-padding-vertical:      0.3em;
$field-padding-horizontal:    0.7em;

$border-radius:               3px !default;

.field-text {
  display: block;
  margin-bottom: 13px;
  font-size: 0;

  &__name {
    display: inline-block;
    margin-bottom: 0.4em;
    font-size: 16px;
    line-height: $line-height;
    font-weight: 700;

    @media (max-width: ($screen-md)) {
      display: block;
    }
  }

  &__input-wrap {
    display: block;

    @media (min-width: ($screen-md)) {
      display: inline-block;
    }

    @media (max-width: ($screen-md)) {
      margin: 0;
    }
  }

  &__input-wrap-name {
    margin-left: 68px;
  }

  &__input-wrap-email {
    margin-left: 60px;
  }

  &__input-wrap-phone {
    margin-left: 33px;
  }

  &__input-wrap-message {
    margin-left: 9px;
  }

  &__input {
    display: inline-block;
    vertical-align: middle;
    // height: 2.125em;
    width: 272px;
    padding: $field-padding-vertical  $field-padding-horizontal;
    line-height: $line-height;
    font-size: 16px;
    font-family: inherit;
    background-color: #fff;
    border: 1px solid $border-color;
    border-radius: $border-radius;
    appearance: textfield;

    &:focus {
      @include field-focus;
    }

    &[disabled],
    &[readonly] {
      cursor: default;
      background-color: $gray-lighter;
    }

    &[type='search']::-webkit-search-cancel-button,
    &[type='search']::-webkit-search-decoration {
      -webkit-appearance: none; /* stylelint-disable-line  property-no-vendor-prefix */
    }

    &[type='number']::-webkit-inner-spin-button,
    &[type='number']::-webkit-outer-spin-button {
      // height: auto;
    }

    &[type='color'] {
      // max-width: 100px;
      padding: 0;
    }

    &::placeholder {
      color: $text-color--muted;
      opacity: 1;
    }

    @at-root textarea#{&} {
      $textarea-height: ($line-height * 2) + ($field-padding-vertical * 2);

      height: 73px;
      min-height: calc(#{$textarea-height} + 2px);
      resize: none;
    }
  }

  &__help-text {
    @include field-help-text;
  }
}
